<template>
  <div class="anime-recommendation">
    <h2 class="section-title" data-aos="fade-up">🎬 动画推荐</h2>
    
    <!-- 当前推荐显示区域 -->
    <div class="current-recommendation">
      <!-- 切换按钮 -->
      <button 
        class="nav-button left-button" 
        @click="prevAnime" 
        aria-label="上一个推荐"
      >
        <LeftOutlined />
      </button>
      
      <!-- 推荐内容 -->
      <div class="recommendation-content">
        <div class="anime-image-container">
          <img 
            :src="currentAnime.imageUrl" 
            :alt="currentAnime.title" 
            class="anime-image" 
            loading="lazy"
          />
        </div>
        <div class="anime-info">
          <h3 class="anime-title">{{ currentAnime.title }}</h3>
          <p class="anime-description">{{ currentAnime.description }}</p>
        </div>
      </div>
      
      <button 
        class="nav-button right-button" 
        @click="nextAnime" 
        aria-label="下一个推荐"
      >
        <RightOutlined />
      </button>
    </div>
    
    <!-- 推荐理由区域 -->
    <div class="recommendation-reason">
      <h4 class="reason-title">推荐理由</h4>
      <p class="reason-text">{{ currentAnime.reason }}</p>
    </div>
    
    <!-- 指示器 -->
    <div class="indicator-container">
      <div 
        v-for="(anime, index) in animeList" 
        :key="anime.id"
        class="indicator"
        :class="{ 'active': currentIndex === index }"
        @click="goToAnime(index)"
      ></div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed } from 'vue';
import { LeftOutlined, RightOutlined } from '@ant-design/icons-vue';

// 定义动画推荐数据类型
interface AnimeRecommendation {
  id: number;
  title: string;
  description: string;
  imageUrl: string;
  reason: string;
}

// 动画推荐数据
const animeList = ref<AnimeRecommendation[]>([
  {
    id: 1,
    title: '死亡笔记',
    description: '一部关于天才与死亡的心理战动画',
    imageUrl: require('../assets/images/animation/death note。jpg.jpg'),
    reason: '智斗启蒙作了属于是，难以超越的经典'
  },
  {
    id: 2,
    title: 'Fate/Zero',
    description: '圣杯战争的史诗篇章',
    imageUrl: require('../assets/images/animation/fz.jpg'),
    reason: '同样的，开启我月丑生涯的启蒙作，也是fate系列最喜欢的一作，切嗣也是我觉得最有魅力的男主角'
  },
  {
    id: 3,
    title: '游戏人生',
    description: '空白永不败北',
    imageUrl: require('../assets/images/animation/no game no life.jpg'),
    reason: '智斗和有趣的神作，剧场版更是传说。但你说的对，我第二季呢'
  },
  {
    id: 4,
    title: 'OVERLORD',
    description: '骨傲天的异世界冒险',
    imageUrl: require('../assets/images/animation/overlord.jpg'),
    reason: '世界观和人设十分有趣，牢骨和旧友的关系是最吸引人的'
  },
  {
    id: 5,
    title: '魔法少女小圆',
    description: '爱的战士虚渊玄的代表作',
    imageUrl: require('../assets/images/animation/圆神.jpg'),
    reason: '军火女王和圆神是这部番吸引我的G点'
  },
  {
    id: 6,
    title: '无职转生',
    description: '关于成长与救赎的异世界物语',
    imageUrl: require('../assets/images/animation/无职转生.jpg'),
    reason: '没什么好说的，异世界的神'
  },
  {
    id: 7,
    title: '想要成为影之实力者',
    description: '迪化最严重的一集',
    imageUrl: require('../assets/images/animation/暗影大人.jpg'),
    reason: '主打一个黄金厕纸有趣就完了'
  },
  {
    id: 8,
    title: '为美好的世界献上祝福！',
    description: '轻松搞笑的异世界喜剧',
    imageUrl: require('../assets/images/animation/素晴.jpg'),
    reason: '这类型的神，什么时候看到和真能不笑呢'
  },
  {
    id: 9,
    title: '某科学的超电磁炮',
    description: '炮姐的传奇故事',
    imageUrl: require('../assets/images/animation/超炮.jpg'),
    reason: '学园都市发生的一切都深刻吸引着我，那还说啥了，黑子是我的'
  },
  {
    id: 10,
    title: '反叛的鲁路修',
    description: '关于权谋与复仇的经典之作',
    imageUrl: require('../assets/images/animation/鲁鲁修.jpg'),
    reason: '你说的对，这部在智斗上略显儿科，但鲁鲁修最后我是真的掉小珍珠了好不好'
  }
]);

// 当前显示的动画索引
const currentIndex = ref(0);

// 计算当前显示的动画
const currentAnime = computed(() => {
  return animeList.value[currentIndex.value];
});

// 切换到上一个动画
const prevAnime = () => {
  currentIndex.value = (currentIndex.value - 1 + animeList.value.length) % animeList.value.length;
};

// 切换到下一个动画
const nextAnime = () => {
  currentIndex.value = (currentIndex.value + 1) % animeList.value.length;
};

// 跳转到指定动画
const goToAnime = (index: number) => {
  currentIndex.value = index;
};
</script>

