<template>
  <div class="banner" :class="{ 'scrolled': scrollProgress > 0 }">
    <!-- 增强的背景效果 -->
    <div class="banner-background" :style="{ opacity: 1 - scrollProgress * 0.8 }"></div>
    
    <!-- 内容区 -->
    <div 
      class="banner-content fade-in"
      :style="{
        opacity: 1 - scrollProgress,
        transform: `translateY(${scrollProgress * 50}px)`
      }"
    >
      <h1 class="banner-title slide-up">Shiki的小站</h1>
      <p class="banner-slogan slide-up">记录日常 & 碎碎念</p>
    </div>
    
    <!-- 滚动指示器 -->
    <div 
      class="banner-scroll-indicator"
      :style="{ opacity: 1 - scrollProgress * 2 }"
      @click="scrollToArticles"
    >
      <CaretDownOutlined class="banner-arrow float" />
    </div>
    
    <!-- 顶部渐变遮罩 -->
    <div 
      class="banner-top-mask"
      :style="{ opacity: scrollProgress * 2 }"
    ></div>
  </div>
</template>

<script setup lang="ts">
import { CaretDownOutlined } from '@ant-design/icons-vue';
import { ref, onMounted, onUnmounted } from 'vue';

// 滚动进度值 (0-1)
const scrollProgress = ref(0);

// 滚动到文章区域
const scrollToArticles = () => {
  const articleSection = document.querySelector('.article-section');
  if (articleSection) {
    articleSection.scrollIntoView({ behavior: 'smooth' });
  }
};

// 处理滚动事件
const handleScroll = () => {
  // 获取滚动位置
  const scrollTop = window.scrollY;
  // 获取banner区域的高度
  const bannerElement = document.querySelector('.banner');
  const bannerHeight = bannerElement instanceof HTMLElement ? bannerElement.offsetHeight : 0;
  
  // 计算滚动进度 (0-1)
  scrollProgress.value = Math.min(scrollTop / (bannerHeight * 0.8), 1);
};

// 组件挂载时添加滚动事件监听
onMounted(() => {
  window.addEventListener('scroll', handleScroll);
});

// 组件卸载时移除滚动事件监听
onUnmounted(() => {
  window.removeEventListener('scroll', handleScroll);
});
</script>

<style scoped>
