<template>
  <div class="shiki-home">
    <!-- 顶部加载指示器 -->
    <div 
      class="page-loader"
      :class="{ 'page-loaded': isLoaded }"
    ></div>
    
    <!-- 顶部Banner -->
    <BannerSection />
    
    <!-- 文章列表区 -->
    <section class="article-section">
      <div class="container">
        <h2 class="section-title" data-aos="fade-up">简单介绍自己</h2>
        <div class="article-list">
          <ArticleCard 
            v-for="(article, index) in articles" 
            :key="article.id"
            :article="article"
            :data-index="index"
            @click="openDetail(article.id)"
            class="article-card-item"
          />
        </div>
      </div>
    </section>
    
    <!-- 文章详情弹窗 -->
    <DetailModal 
      :show="showDetail"
      :detail="currentDetail"
      @close="closeDetail"
    />

    <!-- 回到顶部按钮 -->
    <BackToTop />
    
    <!-- 动画推荐区域 -->
    <section class="anime-section">
      <div class="container">
        <AnimeRecommendation />
      </div>
    </section>

    <!-- 音乐播放器区域 -->
    <section class="music-section">
      <div class="container">
        <h2 class="section-title" data-aos="fade-up">🎵 不听milet的人生是相对失败的</h2>
        <div class="music-player-wrapper">
          <MusicPlayer />
        </div>
      </div>
    </section>

    <!-- 底部区域 -->
    <footer class="footer-section">
      <div class="container">
        <div class="footer-content">
          <div class="haruhi-image">
            <img src="../assets/images/凉宫春日.jpg" alt="凉宫春日" />
          </div>
          <div class="haruhi-quote">
            我对普通的人类没有兴趣，你们当中要是有外星人、未来人、异世界人以及超能力者的话，就尽管来找我吧！
          </div>
          <a href="https://github.com/shiki-desu" target="_blank" class="github-icon">
            <img src="../assets/images/github.png" alt="GitHub" />
          </a>
        </div>
      </div>
    </footer>
  </div>
</template>

<script setup lang="ts">
// 导入组件
import BannerSection from '../components/BannerSection.vue';
import ArticleCard from '../components/ArticleCard.vue';
import DetailModal from '../components/DetailModal.vue';
import BackToTop from '../components/BackToTop.vue';
import MusicPlayer from '../components/MusicPlayer.vue';
import AnimeRecommendation from '../components/AnimeRecommendation.vue';

// 导入外部资源
import { ref, onMounted } from 'vue';
import { Article, getArticles, getArticleById } from '../services/articleService';
import { disablePageScroll, enablePageScroll, addFadeInAnimation } from '../utils/scrollUtils';

// 加载样式
import '../assets/styles/shikiHomeStyles.css';

// 数据和状态
const articles = ref<Article[]>(getArticles());
const showDetail = ref(false);
const currentDetail = ref<Article | null>(null);
const isLoaded = ref(false);

// 打开详情弹窗
const openDetail = (id: number) => {
  const detail = getArticleById(id);
  if (detail) {
    currentDetail.value = detail;
    showDetail.value = true;
    disablePageScroll();
  }
};

// 关闭详情弹窗
const closeDetail = () => {
  showDetail.value = false;
  enablePageScroll();
};

// 页面加载完成
onMounted(() => {
  // 模拟页面加载
  setTimeout(() => {
    isLoaded.value = true;
    
    // 添加卡片的渐入动画
    const cards = Array.from(document.querySelectorAll('.article-card-item')) as HTMLElement[];
    addFadeInAnimation(cards);
  }, 800);
});
</script>