<template>
  <!-- 悬浮图片（放在播放器外部） -->
  <div 
    v-for="img in floatingImages" 
    :key="img.id" 
    class="floating-image"
    :style="img.style"
  >
    <img :src="img.src" :alt="'milet ' + img.id" class="floating-img">
  </div>
  
  <div class="music-player-container">
    <!-- 播放器上半部分图片 -->
    <div class="player-top-image">
      <img src="../assets/images/milet/milet底部.jpg" alt="milet" class="top-image">
    </div>

    <!-- 播放器控制 -->
    <div class="player-controls">
      <!-- 歌曲信息 -->
      <div class="song-info">
        <h3 class="song-title">{{ currentSong.title }}</h3>
        <p class="song-artist">{{ currentSong.artist }}</p>
      </div>

      <!-- 进度条 -->
      <div class="progress-container">
        <span class="progress-time">{{ formatTime(currentTime) }}</span>
        <div class="progress-bar">
          <div 
            class="progress-fill"
            :style="{ width: progressPercentage + '%' }"
          ></div>
          <div 
            class="progress-handle"
            :style="{ left: progressPercentage + '%' }"
            @mousedown="startDragging"
          ></div>
        </div>
        <span class="progress-time">{{ formatTime(duration) }}</span>
      </div>

      <!-- 控制按钮 -->
      <div class="control-buttons">
        <button 
          class="control-btn list-btn"
          @click="togglePlaylist"
          title="查看播放列表"
        >
          <span class="icon">📋</span>
        </button>
        <button 
          class="control-btn prev-btn"
          @click="prevSong"
          title="上一首"
        >
          <span class="icon">⏮️</span>
        </button>
        <button 
          class="control-btn play-btn"
          @click="togglePlay"
          :title="isPlaying ? '暂停' : '播放'"
        >
          <span class="icon">{{ isPlaying ? '⏸️' : '▶️' }}</span>
        </button>
        <button 
          class="control-btn next-btn"
          @click="nextSong"
          title="下一首"
        >
          <span class="icon">⏭️</span>
        </button>
        <button 
          class="control-btn volume-btn"
          @click="toggleVolume"
          @dblclick="() => { volume = volume > 0 ? 0 : 0.7; updateVolume(); }"
          title="音量 (双击静音)"
        >
          <span class="icon">{{ isMuted ? '🔇' : '🔊' }}</span>
        </button>
      </div>
    </div>

    <!-- 播放列表 -->
    <div class="playlist-overlay" v-if="showPlaylist" @click.self="togglePlaylist">
      <div class="playlist-container">
        <div class="playlist-header">
          <h3>播放列表</h3>
          <button class="close-btn" @click="togglePlaylist">×</button>
        </div>
        <div class="playlist-content">
          <div 
            v-for="(song, index) in playlist"
            :key="index"
            class="playlist-item"
            :class="{ 'active': index === currentIndex }"
            @click="playSong(index)"
          >
            <div class="playlist-item-info">
              <div class="playlist-item-title">{{ song.title }}</div>
              <div class="playlist-item-artist">{{ song.artist }}</div>
            </div>
            <div class="playlist-item-duration">{{ song.duration }}</div>
          </div>
        </div>
      </div>
    </div>

    <!-- 音量控制条 -->
    <div class="volume-control" v-if="showVolume">
      <input 
        type="range"
        min="0"
        max="1"
        step="0.01"
        v-model="volume"
        @input="updateVolume"
        class="volume-slider"
      >
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, onMounted, onUnmounted, computed, reactive } from 'vue';

// 定义歌曲类型
interface Song {
  id: number;
  title: string;
  artist: string;
  src: string;
  duration?: string;
}

// 创建音频上下文
const audio = new Audio();

// 播放列表
const playlist = ref<Song[]>([]);

// 当前状态
const currentIndex = ref(0);
const isPlaying = ref(false);
const showPlaylist = ref(false);
const showVolume = ref(false);
const volume = ref(0.7);
const isMuted = ref(false);
const currentTime = ref(0);
const duration = ref(0);
const isDragging = ref(false);

// 当前歌曲
const currentSong = computed(() => {
  if (playlist.value.length === 0) {
    return { title: '暂无歌曲', artist: '', src: '' };
  }
  return playlist.value[currentIndex.value];
});

// 定义悬浮图片的接口类型
interface FloatingImage {
  id: number;
  src: string;
  style: { [key: string]: string };
}

// 悬浮图片数据
const floatingImages = reactive<FloatingImage[]>([
  { id: 1, src: require('../assets/images/milet/milet_01.jpg'), style: {} },
  { id: 2, src: require('../assets/images/milet/milet_02.jpg'), style: {} },
  { id: 3, src: require('../assets/images/milet/milet_03.jpg'), style: {} },
  { id: 4, src: require('../assets/images/milet/milet_04.jpg'), style: {} }
]);

// 随机调整图片位置，避免重叠
const randomizeImagePositions = () => {
  // 随机打乱图片数组
  const shuffledImages = [...floatingImages].sort(() => Math.random() - 0.5);
  
  // 定义左右两侧的图片
  const leftImages = shuffledImages.slice(0, 2);
  const rightImages = shuffledImages.slice(2, 4);
  
  // 设置左侧图片位置 - 覆盖上下部分
  leftImages.forEach((img, index) => {
    img.style.left = `${3 + Math.random() * 2}%`; // 3%-5% 随机位置
    img.style.right = 'auto';
    // 一个在15%-35%（上部分），一个在55%-75%（下部分）
    const topPosition = index === 0 ? 
      `${15 + Math.floor(Math.random() * 20)}%` : 
      `${55 + Math.floor(Math.random() * 20)}%`;
    img.style.top = topPosition;
    img.style.animationDelay = `${Math.random() * 2}s`;
    img.style.zIndex = `${10 + index}`; // 确保层级不同
  });
  
  // 设置右侧图片位置 - 覆盖上下部分
  rightImages.forEach((img, index) => {
    img.style.left = 'auto';
    img.style.right = `${3 + Math.random() * 2}%`; // 3%-5% 随机位置
    // 一个在25%-45%（上部分），一个在65%-85%（下部分）
    const topPosition = index === 0 ? 
      `${25 + Math.floor(Math.random() * 20)}%` : 
      `${65 + Math.floor(Math.random() * 20)}%`;
    img.style.top = topPosition;
    img.style.animationDelay = `${Math.random() * 2}s`;
    img.style.zIndex = `${10 + index}`; // 确保层级不同
  });
};

// 定时随机变换图片位置
let positionInterval: number | null = null;
const startPositionAnimation = () => {
  if (positionInterval) clearInterval(positionInterval);
  positionInterval = window.setInterval(randomizeImagePositions, 8000);
};

const stopPositionAnimation = () => {
  if (positionInterval) {
    clearInterval(positionInterval);
    positionInterval = null;
  }
};

// 进度百分比
const progressPercentage = computed(() => {
  if (duration.value === 0) return 0;
  return (currentTime.value / duration.value) * 100;
});

// 格式化时间
const formatTime = (seconds: number) => {
  const mins = Math.floor(seconds / 60);
  const secs = Math.floor(seconds % 60);
  return `${mins}:${secs < 10 ? '0' + secs : secs}`;
};

// 解析歌曲标题和艺术家
const parseSongInfo = (fileName: string): { title: string; artist: string } => {
  // 处理不同格式的文件名
  let artist = 'milet';
  let title = fileName.replace('.mp3', '');
  
  // 处理特殊格式如 "milet,Aimer,幾田りら - おもかげ"
  if (fileName.includes(' - ')) {
    const parts = fileName.split(' - ');
    artist = parts[0];
    title = parts[1].replace('.mp3', '');
  } 
  // 处理 "MAN WITH A MISSION,milet - 絆ノ奇跡"
  else if (fileName.includes(',')) {
    const parts = fileName.split('.mp3');
    title = parts[0];
  }
  
  return { title, artist };
};

// 加载音乐文件
const loadMusicFiles = async () => {
  // 获取音乐文件夹中的所有mp3文件
  const musicFiles = [
    'MAN WITH A MISSION,milet - 絆ノ奇跡.mp3',
    'milet & Aimer & 幾田りら - おもかげ (produced by Vaundy).mp3',
    'milet - Again and Again.mp3',
    'milet - Anytime Anywhere.mp3',
    'milet - Bluer.mp3',
    'milet - Come Here (Session1).mp3',
    'milet - December.mp3',
    'milet - Final Call.mp3',
    'milet - Fine Line.mp3',
    'milet - Flare.mp3',
    'milet - Fly High.mp3',
    'milet - Grab the air.mp3',
    'milet - HALFWAY.mp3',
    'milet - Hey Song.mp3',
    'milet - Higher.mp3',
    'milet - Hit the Lights.mp3',
    'milet - I still.mp3',
    'milet - Nobody Knows.mp3',
    'milet - Noёl In July.mp3',
    'milet - On the Edge.mp3',
    'milet - One Reason.mp3',
    'milet - One Touch.mp3',
    'milet - Ordinary days.mp3',
    'milet - Outsider.mp3',
    'milet - Parachute.mp3',
    'milet - Prover.mp3',
    'milet - Rewrite.mp3',
    'milet - SEVENTH HEAVEN.mp3',
    'milet - Tell me.mp3',
    'milet - The Love We\'ve Made.mp3',
    'milet - Time Is On Our Side.mp3',
    'milet - Walkin\' In My Lane.mp3',
    'milet - Waterfall.mp3',
    'milet - Who I Am.mp3',
    'milet - You & I.mp3',
    'milet - Your Light.mp3',
    'milet - b r o k e n.mp3',
    'milet - checkmate.mp3',
    'milet - hanataba.mp3',
    'milet - inside you - From THE FIRST TAKE.mp3',
    'milet - inside you.mp3',
    'milet - jam.mp3',
    'milet - us - From THE FIRST TAKE.mp3',
    'milet - us.mp3',
    'milet,Aimer,幾田りら - おもかげ (produced by Vaundy).mp3',
    'milet,MAN WITH A MISSION - コイコガレ.mp3'
  ];

  // 创建播放列表
  playlist.value = musicFiles.map((file, index) => {
    const { title, artist } = parseSongInfo(file);
    return {
      id: index + 1,
      title,
      artist,
      src: require(`../assets/music/${file}`)
    };
  });

  // 如果有歌曲，初始化第一首
  if (playlist.value.length > 0) {
    await loadSong(currentIndex.value);
  }
};

// 加载指定歌曲
const loadSong = async (index: number) => {
  if (index < 0 || index >= playlist.value.length) return;
  
  currentIndex.value = index;
  const song = playlist.value[index];
  
  audio.src = song.src;
  audio.volume = volume.value;
  
  // 尝试获取歌曲时长
  try {
    await audio.load();
    if (audio.duration) {
      duration.value = audio.duration;
      song.duration = formatTime(audio.duration);
    }
  } catch (error) {
    console.error('加载歌曲失败:', error);
  }
};

// 播放/暂停切换
const togglePlay = async () => {
  if (playlist.value.length === 0) return;
  
  if (isPlaying.value) {
    audio.pause();
  } else {
    try {
      await audio.play();
    } catch (error) {
      console.error('播放失败:', error);
      // 处理浏览器的自动播放限制
      alert('请点击播放器以开始播放音乐');
    }
  }
  isPlaying.value = !isPlaying.value;
};

// 播放指定歌曲
const playSong = async (index: number) => {
  const wasPlaying = isPlaying.value;
  await loadSong(index);
  if (wasPlaying) {
    await togglePlay();
  }
  showPlaylist.value = false;
};

// 下一首
const nextSong = () => {
  const nextIndex = (currentIndex.value + 1) % playlist.value.length;
  playSong(nextIndex);
};

// 上一首
const prevSong = () => {
  const prevIndex = (currentIndex.value - 1 + playlist.value.length) % playlist.value.length;
  playSong(prevIndex);
};

// 切换播放列表显示
const togglePlaylist = () => {
  showPlaylist.value = !showPlaylist.value;
};

// 切换音量控制显示
const toggleVolume = () => {
  showVolume.value = !showVolume.value;
};

// 更新音量
const updateVolume = () => {
  audio.volume = volume.value;
  isMuted.value = volume.value === 0;
};

// 开始拖动进度条
const startDragging = () => {
  isDragging.value = true;
};

// 设置进度
const setProgress = (percentage: number) => {
  if (duration.value === 0) return;
  
  const newTime = (percentage / 100) * duration.value;
  currentTime.value = newTime;
  audio.currentTime = newTime;
};

// 监听鼠标移动（拖动进度条时）
const handleMouseMove = (e: MouseEvent) => {
  if (!isDragging.value) return;
  
  const progressBar = document.querySelector('.progress-bar') as HTMLElement;
  if (!progressBar) return;
  
  const rect = progressBar.getBoundingClientRect();
  const x = e.clientX - rect.left;
  const percentage = Math.max(0, Math.min(100, (x / rect.width) * 100));
  
  // 只更新UI，不更新音频.currentTime
  currentTime.value = (percentage / 100) * duration.value;
};

// 监听鼠标释放（结束拖动进度条时）
const handleMouseUp = () => {
  if (!isDragging.value) return;
  
  setProgress(progressPercentage.value);
  isDragging.value = false;
};

// 音频事件监听
const setupAudioEvents = () => {
  // 更新当前播放时间
  audio.ontimeupdate = () => {
    if (!isDragging.value) {
      currentTime.value = audio.currentTime;
    }
  };

  // 歌曲结束时自动播放下一首
  audio.onended = () => {
    nextSong();
  };

  // 加载完成时获取时长
  audio.onloadedmetadata = () => {
    duration.value = audio.duration;
    if (currentSong.value && 'duration' in currentSong.value) {
      (currentSong.value as Song).duration = formatTime(audio.duration);
    }
  };
};

// 生命周期钩子
onMounted(() => {
  loadMusicFiles();
  setupAudioEvents();
  
  // 添加全局鼠标事件监听
  document.addEventListener('mousemove', handleMouseMove);
  document.addEventListener('mouseup', handleMouseUp);
  
  // 初始化悬浮图片动画
  randomizeImagePositions();
  startPositionAnimation();
});

onUnmounted(() => {
  // 清理资源
  audio.pause();
  document.removeEventListener('mousemove', handleMouseMove);
  document.removeEventListener('mouseup', handleMouseUp);
  
  // 清理定时器
  stopPositionAnimation();
});
</script>

