<!-- eslint-disable -->
<template>
  <a-card hoverable class="article-card" @click="handleClick">
    <!-- 封面图区域 -->
    <template #cover>
      <div class="image-container">
        <img
          :src="article.coverImg"
          :alt="article.title"
          class="article-image"
          loading="lazy"
        />
        <!-- 悬停时的渐变遮罩 -->
        <div class="image-overlay"></div>
      </div>
    </template>

    <!-- 卡片内容 -->
    <a-card-meta
      :title="article.title"
      :description="article.date"
      class="card-meta"
    />

    <!-- 点击指示器 -->
    <div class="click-indicator">
      <span>点击查看详情</span>
    </div>
  </a-card>
</template>

<script setup lang="ts">
import type { PropType } from "vue";

// 定义文章数据类型
interface Article {
  id: number;
  title: string;
  date: string;
  content: string;
  coverImg: string;
}

// 接收文章数据作为prop
// eslint-disable-next-line no-undef
const props = defineProps({
  article: {
    type: Object as PropType<Article>,
    required: true,
  },
});

// 定义点击事件
// eslint-disable-next-line no-undef
const emit = defineEmits(["click"]);

const handleClick = () => {
  emit("click", props.article.id);
};
</script>

<style scoped>
/* 卡片基础样式 */
.article-card {
  border-radius: var(--radius-lg);
  overflow: hidden;
  cursor: pointer;
  transition: all var(--transition-normal);
  background-color: var(--bg-color);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-light);
  position: relative;
}

/* 卡片悬停效果 */
.article-card:hover {
  transform: translateY(-6px);
  box-shadow: var(--shadow-medium);
  border-color: var(--primary-light);
}

/* 卡片点击效果 */
.article-card:active {
  transform: translateY(-3px);
}

/* 图片容器 - 设置固定宽高比 */
.image-container {
  position: relative;
  width: 100%;
  padding-bottom: 66.67%; /* 3:2的宽高比 */
  overflow: hidden;
  transition: all var(--transition-normal);
}

/* 悬停时图片缩放效果 */
.article-card:hover .image-container {
  transform: scale(1.02);
}

/* 文章图片样式 */
.article-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform var(--transition-slow);
}

/* 图片悬停缩放 */
.article-card:hover .article-image {
  transform: scale(1.05);
}

/* 图片悬停渐变遮罩 */
.image-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    135deg,
    rgba(24, 144, 255, 0) 0%,
    rgba(24, 144, 255, 0.1) 100%
  );
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.article-card:hover .image-overlay {
  opacity: 1;
}

/* 卡片元数据样式 */
.card-meta {
  padding: var(--spacing-lg);
  transition: all var(--transition-normal);
}

/* Ant Design 卡片标题样式覆盖 */
:deep(.ant-card-meta-title) {
  font-size: var(--font-size-lg);
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: var(--spacing-xs);
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: color var(--transition-fast);
}

:deep(.ant-card-meta-title:hover) {
  color: var(--primary-color);
}

/* Ant Design 卡片描述样式覆盖 */
:deep(.ant-card-meta-description) {
  font-size: var(--font-size-sm);
  color: var(--text-tertiary);
  margin-bottom: 0;
}

/* 点击指示器 */
.click-indicator {
  position: absolute;
  bottom: 10px;
  right: 15px;
  opacity: 0;
  transform: translateY(10px);
  transition: all var(--transition-normal);
}

.article-card:hover .click-indicator {
  opacity: 1;
  transform: translateY(0);
}

.click-indicator span {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.9);
  color: var(--primary-color);
  font-size: var(--font-size-xs);
  padding: 4px 10px;
  border-radius: var(--radius-full);
  box-shadow: var(--shadow-light);
}

/* 加载动画 */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* 延迟动画效果，用于多个卡片时产生错开的动画效果 */
.article-card {
  animation: fadeInUp 0.5s ease forwards;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .article-card {
    border-radius: var(--radius-md);
  }

  .card-meta {
    padding: var(--spacing-md);
  }

  :deep(.ant-card-meta-title) {
    font-size: var(--font-size-base);
  }
}
</style>
